body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
  background-color: white;
}
.header__logo {
  height: 30px;
  margin-left: 20px;
  width: 120px;
}
.header__input {
  display: flex;
  align-items: center;
  width: 25%;
}
.header__input > input {
  flex: 1 1;
  outline: none;
}

.header__left {
  display: flex;
  align-items: center;
  margin-left: 8px;
}
.header__icons {
  display: flex;
}
.header__icon {
  margin-right: 25px;
}
.input__button {
  width: 10% !important;
  background-color: #f8f8f8;
  border: 1px soild lightgray;
  color: rgba(0, 0, 0, 0.396);
  height: 30px !important;
}
@media only screen and (max-width: 700px) {
  .input__button {
    margin-left: 2px;
  }
}
@media only screen and (max-width: 700px) {
  .header__icons {
    margin-right: 22px;
  }
}
@media only screen and (max-width: 700px) {
  #video__icon {
    margin-left: 79px;
  }
}

