  }
} */
@media only screen and (max-width: 700px) {
  .header__input > input {
    width: 140px;
  }
}

.sidebar {
  flex: 0.2 1;
}

.sidebar > hr {
  background-color: grey;
  margin-top: 7px;
  border: none;
  margin-bottom: 2px;
}

.sidebar-row {
  display: flex;
  align-items: center;
  padding: 11px 20px;
}
.sidebar-row-icon {
  color: grey;
  font-size: large !important;
}
.sidebar-row-title {
  font-size: 15px;
  font-weight: 5px;
  margin-left: 20px;
  margin-top: 5px;
  flex: 1 1;
}
.sidebar-row:hover {
  background-color: rgb(246, 224, 224);
  cursor: pointer;
}
.sidebar-row:hover > .sidebar-row-icon {
  color: red;
}
.sidebar-row:hover > .sidebar-row-title {
  font-weight: bold;
}

.sidebar-row.selected {
  background-color: rgb(246, 224, 224);
  margin: 2px;
}

.sidebar-row.selected > .sidebar-row-icon {
  color: red;
}
.sidebar-row.selected > .sidebar-row-title {
  font-weight: bold;
}

.search__page {
  flex: 0.8 1;
  background-color: #f9f9f9;
  padding: 10px 15px;
  align-items: center;
}
.search__filter {
  display: flex;
}
.search__filter > h2 {
  color: rgb(72, 72, 72);

  font-size: 25px !important;
}
.search__filter > .search__filtericon {
  margin-top: 7px;
  margin-left: 9px;
  cursor: pointer;
}

.channelrow {
  display: flex;
  width: 70%;
  align-items: center;
}
.channelrow__text {
  display: flex;
  flex-direction: column;
}
.channelrow__text > p {
  font-size: small !important;
  color: rgb(51, 51, 51);
}
.channelrow__logo {
  height: 80px;
  width: 80px;
  margin: 10px 20px;
}
.channelrow__text > h4 {
  display: flex;
  align-items: center;
}

.videorow {
  display: flex;
  align-items: center;
  margin-top: 18px;
}
.videorow > .videorow__text {
  margin-left: 4px;
  margin-top: -8px;
}
.mixedtext {
  color: #606060;
  font-size: 12px;
  line-height: 4px;
}

.videorow > img {
  width: 246px;
  height: 138px;
  object-fit: contain;
}
.description {
  font-size: 15px;
  color: #606060;
  margin-top: -7px;
}

.recommended-videos {
  flex: 0.8 1;
  padding: 10px 8px;
  background-color: #f9f9f9;
  margin-bottom: 8px;
}
.recommended-videos > h2 {
  margin-left: 9px;
  margin-top: 12px;
}
.recommended__videos {
  display: flex;
  flex-wrap: wrap;
  margin: 1px;
}

.videocard {
  margin-bottom: 22px;
  margin-left: 19px;
  margin-top: 20px;
}
.videocard__info {
  margin-top: 9px;
  display: flex;
}
.videocard__img {
  width: 277px;
  height: 180px;
}
.lastpara {
  margin-top: -19px;
}
.videocard__text {
  margin-left: 12px;
}
.videocard__text > h4 {
  font-size: 19px;
}
.videocard__text > p {
  color: grey;
  font-size: 14px;
}
.avatar {
  width: 30px !important;
  height: 30px !important;
}

